.style__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.style__name {
  @include font-s;

  cursor: pointer;

  &--active {
    font-weight: $font-weight-bold;
  }
}

@media (min-width: $tablet-width) {
  .style__name {
    margin-right: 30px;
    margin-bottom: 20px;
  }
}
