.actions__item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.actions__button {
  margin: 0;
  padding: 0;

  outline: none;
  cursor: pointer;

  &:hover,
  &:focus {
    border: none;
    outline: 1px solid $alt-color;
    outline-offset: 2px;
  }

  &:active {
    outline-color: $brand-color;
  }
}

.actions__button-find {
  width: 18px;
  height: 18px;

  background-image: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.actions__button-basket {
  width: 20px;
  height: 18px;

  background-image: url("../img/icons/basket.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

@media (min-width: $tablet-width) {
  .actions__button-find {
    width: 20px;
    height: 20px;
  }

  .actions__button-basket {
    width: 22px;
    height: 20px;
  }

  .actions__item {
    margin-right: 32px;
  }
}
